function PrivacyPolicy() {
  return (
    <div>
        <Header />
<SubHeader />
        <div className="container PrivacyPolicy">
          

				<h1 className="center my-4">Privacy Policy</h1>
<p>This statement of privacy (“Privacy Policy”) describes how Hellomitr Technology Pvt Ltd (hereinafter referred to as “we”, “us” “our”) collect, use, and disclose information pertaining to you- the user (hereinafter referred to as “you”, “your”) obtained via this website www.hellomitr.com “Website/app/iOS”.</p>
<p>Please read the following to learn about our practice of secure collection, use, disclosure and dissemination of information practices.</p>
<p>By visiting this Website you agree to be bound by the terms and conditions of this Privacy Policy. If you do not agree, please do not use or access our Website. This Privacy Policy is incorporated into and subject to our Terms of Use (“Terms of Use”).</p>
<h3>1. Consent</h3>
<p>By using the Website and/ or by providing your information, you consent to the collection and use of the information you disclose on the Website in accordance with this Privacy Policy, including but not limited to Your consent for sharing your information as per this Privacy Policy. If you disclose any personal information relating to other people to us, you represent that you have the authority to do so and to permit us to use the information in accordance with this Privacy Policy.</p>
<h3>2. Amendment in the Privacy Policy</h3>
<p>Our Privacy Policy is subject to change at any time without notice. To make sure you are aware of any changes, please review this policy periodically.</p>
<h3>3. Collection of Personally Identifiable Information and other Information</h3>
<p>When you use our Website, we collect and store your personal information such as name, contact number, email address etc. which is provided by you from time to time. Our primary goal in doing so is to be able to contact you for the services requested by you and to provide you a safe, efficient, smooth and customized experience.</p>
<p>In general, you can browse the Website without telling us who you are or revealing any personal information about yourself. Once you give us your personal information, you are not anonymous to us. Where possible, we indicate which fields are required and which fields are optional for us to contact you. You always have the option to not provide information, however, in such an instance we will not be able to contact you.</p>
<p>If you choose to write a mail to us “helpline@hellomitr.com” or leave feedback, we will collect that information you provide to us.</p>
<h3>4. Use of Demographic / Profile Data &amp; related Information</h3>
<p>We use personal information to provide the services you request. To the extent we use your personal information to market to you, we will provide you the ability to opt-out of such uses if you write us a mail at helpline@hellomitr.com</p>
<p>We identify and use your IP address to help diagnose problems with our server, and to administer our Website. Your IP address is also used to help identify you and to gather broad demographic information. We also use this information to do internal research on our users’ demographics, interests, and behaviour to better understand, protect and serve our users. This information is compiled and analysed on an aggregated basis. This information may include the URL that you just came from (whether this URL is on our Website or not), which URL you next go to (whether this URL is on our Website or not), your computer browser information, and your IP address.</p>
<p>We retain any information as necessary to contact you and provide support as permitted by law.</p>
<h3>5. Sharing of personal information</h3>
<p>We may share personal information with our affiliates. We do not disclose your personal information to third parties for their marketing and advertising purposes without your explicit consent.</p>
<p>We may disclose personal information if required to do so by law or in the good faith belief that such disclosure is reasonably necessary to respond to subpoenas, court orders, or other legal process. We may disclose personal information to law enforcement offices, third party rights owners, or others in the good faith belief that such disclosure is reasonably necessary to: enforce our Terms of Use or Privacy Policy; respond to claims that an advertisement, posting or other content violates the rights of a third party; or protect the rights, property or personal safety of our users or the general public.</p>
<p>We and our affiliates will share some or all of your personal information with another business entity should we (or our assets) plan to merge with, or be acquired by that business entity, or re-organization, amalgamation, restructuring of business. Should such a transaction occur that other business entity (or the new combined entity) will be required to follow this Privacy Policy with respect to your personal information.</p>
<h3>6. Redirects to Other Sites</h3>
<p>Our Website may contain redirect links to other websites that may collect personally identifiable information about you. We are not responsible for the privacy practices or the content of those linked websites.</p>
<h3>7. Security Precautions</h3>
<p>Our Website has stringent security measures in place to protect the loss, misuse, and alteration of the information under our control. Once your information is in our possession, we adhere to strict security guidelines, protecting it against unauthorized access.</p>
<h3>8. Cookies</h3>
<p>A “cookie” is a small piece of information stored by a web server on a web browser so it can be later read back from that browser. Cookies are useful for enabling the browser to remember information specific to a given user. We place both permanent and temporary cookies in your computer’s hard drive. The cookies do not contain any of your personally identifiable information.</p>
<p>We use data collection devices such as “cookies” on certain pages of the Website to help analyse our web page flow, measure promotional effectiveness, and promote trust and safety. “Cookies” are small files placed on your hard drive that assist us in providing our services. We offer certain features that are only available through the use of a “cookie”. Cookies can also help us provide information that is targeted to your interests. Most cookies are “session cookies,” meaning that they are automatically deleted from your hard drive at the end of a session. You are always free to decline our cookies if your browser permits, although in that case you may not be able to use certain features on the Website.</p>
<p>Additionally, you may encounter “cookies” or other similar devices on certain pages of the Website that are placed by third parties. We do not control the use of cookies by third parties.</p>
<h3>9. Opt-Out or choice</h3>
<p>We provide all users with the opportunity to opt-out of receiving non-essential (promotional, marketing-related) communications from us or on behalf of our partners.</p>
<p>If you want to remove your contact information from our server or want to unsubscribe to any newsletters or alerts from us, please contact us on the email address “helpline@hellomitr.com”</p>
<p>We may use third-party advertising companies to serve ads when you visit our Website. These companies may use information (not including your name, address, email address, or telephone number) about your visits to this and other websites in order to provide advertisements about goods and services of interest to you.</p>
<h3>11. Resolution Officer</h3>
<p>In accordance with Information Technology Act 2000 and rules made there under, the name and contact details of the Resolution&nbsp; Officer are provided below:</p>
<p>Mr. Manveer verma<br />
Hellomitr Technology Pvt Ltd.<br />
Noval Tech Park, #46/4 Hosur Rd ,GB Palya Bangalore</p>
<p>Karnataka 560068 I<b>NDIA</b></p>
<p>Phone: 18005728211<br />
Email: compliance@hellomitr.com<br />
Time: Mon – Sat (9:00 – 18:00)</p>
<h3>12. Any queries?</h3>
<p className="mb-5">Please contact us regarding any questions regarding this statement</p>
        </div>
        <Footer/>
    </div>
  )
}

